<script>
import {createTimbreDB} from "../../js/TimbreDB"
import BasicCanvas from "@/components/canvas/BasicCanvas";
import {formatTime} from "@/js/ultilities";

export default {
  extends: BasicCanvas,
  name: "TimbreSpaceCanvas",
  methods:{
    refresh(){
      this.canvasData.setSparseView();
    },
    initSpace(pca, center){
      this.timbreDB = createTimbreDB(pca, center);
    },
    paint_all_data(cleanFirst=0, mode="general"){
      this.paint(cleanFirst, mode);
      this.paintTimbreDB();
    },
    changeTrk(_this, trk){
      _this.canvasData.setSelectedTrk(trk);
    },
    updateColor(_this, color){
      _this.canvasData.updateColor(color);
      _this.paint_all_data(1, "all");
    },
    paintTimbreDB(){
      let centers = this.timbreDB.getCenters();
      for(let i = 0; i < centers.data.length; ++i){
        let x, y, tag;
        [x, y] = centers.data[i];
        tag = centers.tag[i];
        this.drawEmbeddingCenter(x, y, tag, i);
      }

    },
    drawEmbeddingCenter(x, y, t, c){
      let ctx = this.ctx;
      ctx.beginPath();
      ctx.fillStyle = this.canvasData.alphaColors[c + 1];
      let pos = this.canvasData.data2pos(x * 1000, y* 1000);
      ctx.arc(pos[0], pos[1],this.brushSize * 4, 0,2*Math.PI);
      ctx.fill();
      ctx.beginPath();
      ctx.strokeStyle = "black"
      ctx.arc(pos[0], pos[1],this.brushSize * 4, 0,2*Math.PI);
      ctx.stroke();
      ctx.fillStyle = "black";
      ctx.font = "20px serif";
      ctx.fillText(t, pos[0], pos[1])
    }
  }
}
</script>

