<template>
    <div class="canvasLCss">
      <el-affix :offset="10">
        <div class="canvasFixCss" >
          <div class="canvasPianoLCss" ref="canvasPianoLCss">
            <piano-canvas ref="piano"></piano-canvas>
          </div>
          <div class="canvasPianoRCss">
            <div class="canvasTimeTickerTCss" ref="canvasTimeTickerTCss">
              <time-ticker-canvas ref="timbreTicker"></time-ticker-canvas>
            </div>
            <div class="canvasTimeTickerBCss" ref="canvasLFix">
              <piano-roll-canvas ref="testCanvas" canvasName="timbreSpaceCVS"
                :W="canvasLCssW" :H="canvasLCssH" :blockY="true"
                       @pianoPanelAction="pianoPanelAction"
                       @timbrePanelAction="timbrePanelAction"
                       @timbreSpaceAction="timbreSpaceAction"></piano-roll-canvas>
              </div>
          </div>
        </div>
      </el-affix>
      <div class="lControllerCss">
        <piano-panel :sources="sources" ref="pianoPanel"
                     @pianoRollAction="pianoRollAction"
                     @timbrePanelAction="timbrePanelAction"
                     @timbreSpaceAction="timbreSpaceAction"
                     @updateCanvas="updateCanvas"
                     @refreshCanvas="refreshCanvas"></piano-panel>
      </div>
    </div>
    <div class="canvasRCss">
      <el-affix :offset="10">
        <div class="canvasFixCss" ref="canvasRFix">
          <timbre-space-canvas ref="timbrePalette" :brushSize="2.4" :strokeSize="0.2" canvasName="timbreSpaceCVS"
               :W="canvasRCssW" :H="canvasRCssH"></timbre-space-canvas>
        </div>
      </el-affix>
      <div class="rControllerCss">
        <timbre-panel :sources="sources" ref="timbrePanel" @pianoRollAction="pianoRollAction"
                      @timbreSpaceAction="timbreSpaceAction"></timbre-panel>
      </div>
    </div>

</template>

<script>
import TimbrePanel from '@/components/panel/TimbrePanel';
import PianoPanel from '@/components/panel/PianoPanel';

import axios from "axios";
import TimeTickerCanvas from "@/components/canvas/TimeTickerCanvas";
import PianoCanvas from "@/components/canvas/PianoCanvas";
import TimbreSpaceCanvas from "@/components/canvas/TimbreSpaceCanvas";
import PianoRollCanvas from "@/components/canvas/PianoRollCanvas";

export default {
  name: "DoodlePanel",

  components: {
    PianoRollCanvas,
    TimbreSpaceCanvas,
    PianoCanvas,
    TimeTickerCanvas,
    TimbrePanel,
    PianoPanel,
  },
  data(){
    return {
      canvasLCssW : 100,
      canvasLCssH : 100,
      canvasRCssW : 100,
      canvasRCssH : 100,
      sources : [{"id" : 1, "label" : "Undefined"}],
      //sources : [{"id" : 2, "label" : "Trumpet"}, {"id" : 0, "label" : "Trombone"},
      //              {"id" : 1, "label" : "Horn"}],
    }
  },
  mounted() {
    let canvasLFix = this.$refs.canvasLFix;
    let canvasRFix = this.$refs.canvasRFix;
    this.canvasLCssW = canvasLFix.clientWidth;
    this.canvasLCssH = canvasLFix.clientHeight;
    this.canvasRCssW = canvasRFix.clientWidth;
    this.canvasRCssH = canvasRFix.clientHeight;
    this.$refs.testCanvas.emptyCanvas(this.canvasLCssW, this.canvasLCssH, null);
    this.$refs.timbrePalette.emptyCanvas(this.canvasRCssW, this.canvasRCssH, null);
    this.$refs.timbreTicker.setSize(this.$refs.canvasTimeTickerTCss.clientWidth, this.$refs.canvasTimeTickerTCss.clientHeight);
    this.$refs.piano.setSize(this.$refs.canvasPianoLCss.clientWidth,
        this.$refs.canvasPianoLCss.clientHeight,
        this.$refs.canvasTimeTickerTCss.clientHeight);
    //this.pianoRollAction({"func": "emptyCanvas", "arg" : null});
    //this.timbreSpaceAction({"func":"emptyCanvas", "arg": null});
    //this.load_data();
  },
  methods:{
    refreshCanvas(val){
      if(val == null){
        this.$refs.testCanvas.emptyCanvas(this.canvasLCssW, this.canvasLCssH, null);
        this.$refs.timbrePalette.emptyCanvas(this.canvasRCssW, this.canvasRCssH, null);
      }else{
        let pianoroll = {"data" : val.pianoRoll, "color" : val.color};
        let timbre = {"data" : val.timbre, "color" : val.color};
        this.$refs.timbrePalette.initSpace(val.pca, val.center);
        this.$refs.testCanvas.emptyCanvas(this.canvasLCssW, this.canvasLCssH, pianoroll);
        this.$refs.timbrePalette.emptyCanvas(this.canvasRCssW, this.canvasRCssH, timbre);

      }

    },
    updateCanvas(val){
      this.$refs.timbreTicker.refresh(val[0], val[2]);
      this.$refs.piano.refresh(val[1], val[3]);
    },

    pianoPanelAction(val) {
      this.$refs.pianoPanel.callFunc(val);
    },
    pianoRollAction(val) {
      this.$refs.testCanvas.callFunc(val);
    },
    timbrePanelAction(val) {
      this.$refs.timbrePanel.callFunc(val);
    },
    timbreSpaceAction(val) {
      this.$refs.timbrePalette.callFunc(val);
    },
  }
}
</script>

<style lang="scss">
html, body{
  width: 100%;
  height: 100%;
  background-color: #84a2d3;
  //  background-color: #84a2d3;
}
.canvasLCss{
  float: left;
  position: relative;
  width: 60%;
  height: 420px;
  margin-left: 5px;
  margin-top: 5px;
  background-color: rgba(232, 232, 232, 0.99);
  box-shadow: 0 0 3px 3px #666;

}
.canvasRCss{
  float: left;
  position: relative;
  width: 30%;
  height: 420px;
  margin-left: 30px;
  margin-top: 5px;
  background-color: rgba(232, 232, 232, 0.99);
  box-shadow: 0 0 3px 3px #666;

}
.canvasFixCss{
  width: 100%;
  height: 415px;
}
.canvasTimeTickerTCss{
  width: 100%;
  height: 15px;
}
.canvasTimeTickerBCss{
  width: 100%;
  height: 400px;
}
.canvasPianoLCss{
  width: 5%;
  height: 415px;
  float: left;
}
.canvasPianoRCss{
  width: 95%;
  height: 415px;
  float: left;
}
.lControllerCss{
  background-color: #b4ccef;
  width: 100%;
  height: 250px;
  margin-top: 10px;

}
.rControllerCss{
 background-color: #b4ccef;
  width: 100%;
  height: 250px;
  margin-top: 10px;

}
.controller{
  float: left;
  position: relative;
  width: 620px;
  height: 100%;
  margin-left: 10px;
  box-shadow: -7px 0px 5px -5px #333;
  background-color: white;
}



</style>