<template>
    <div class="xlargeWCss">
      <el-dialog v-model="dialogFormVisible" title="Revise result...">
        <el-form >
          <el-timeline>
            <el-timeline-item timestamp="Step 1">
              <el-button type="danger" @click="onClickDownloadData" v-loading="loading">Download</el-button>
                  the data file
            </el-timeline-item>
            <el-timeline-item timestamp="Step 2">
                    Open this
                    <a href="https://colab.research.google.com/github/anonymous-16/santouguai_v03_server/blob/main/santouguai_v03_step2.ipynb">link</a>
                    in a new window, upload the data file and run the model to get the result url.
            </el-timeline-item>
            <el-timeline-item timestamp="Step 3">
              <el-upload
                    ref="uploadRef"
                    action=""
                    :limit="1"
                    v-loading="loading"
                    :on-exceed="handleExceed"
                    :on-change="onChangeFile"
                    :auto-upload="false">
                    <el-button type="success"  size="small">Upload</el-button>the result file (output_step2.zip) and click 'confirm'
              </el-upload>
            </el-timeline-item>
          </el-timeline>
        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogFormVisible = false">Cancel</el-button>
            <el-button type="primary" @click="onClickConfirm">Confirm</el-button>
          </span>
        </template>
      </el-dialog>
    </div>
</template>

<script>
import JSZip from "jszip";
import { saveAs } from 'file-saver';
import {encode} from '../../js/ultilities';

export default {
  name: "SaveChangeDialog",
  data(){
    return{
      dialogFormVisible : false,
      jsonData : null,
      loading : false,
    }
  },
  methods:{
    setJsonData(val){
      this.jsonData = {"data" : val, "sourcesNum" : this.sourcesNum};
    },
    setRepData(val){
      //this.repData = val.data;
      this.sourcesNum = val.sourcesNum;
      this.audioBlob = val.mix;
    },
    showDialog(){
      this.dialogFormVisible = true;
    },
    onClickDownloadData(evt){
      if(this.jsonData == null || this.audioBlob == null){
        this.$message.error("ahhhhhhhhhhhhhhhhhhhhhhhh' ()");
        return;
      }
      this.loading = true;
      const zip = new JSZip();
      let str = encode(JSON.stringify(this.jsonData));
      let json_blob = new Blob( [ str ], {type: 'application/octet-stream'});
      let json_dataPath = "revised_data/data.json"
      zip.file(json_dataPath, json_blob);
      let audio_path = "revised_data/mix.wav";
      zip.file(audio_path, this.audioBlob);
      zip.generateAsync({type: "blob", compression: "DEFLATE", compressionOptions: {level: 9}}).then((content) => {
          saveAs(content, "revised_data.zip");
          this.loading = false;
        });
    },
    onChangeFile(file) {
      file = file.raw;
      let isVali = (file.type === 'application/zip' && file.name === 'output_step2.zip');
      if (!isVali) {
        this.$message.error("Please select 'output_step2.zip' ()");
        this.$refs.transferUploadRef.clearFiles()
        this.selectedFile = null;
      }else{
        this.selectedFile = file;
      }

    },
    handleExceed(files){
      this.$refs.uploadRef.clearFiles()
      this.$refs.uploadRef.handleStart(files[0])
    },
    onClickConfirm(evt){
      let file = this.selectedFile;
      if(file == null){
        this.$message.error("Please select the file 'output_step2.zip' ()");
        return;
      }
      this.loading = true;
      let arReader = new FileReader();
      arReader.readAsArrayBuffer(file);
      let _this = this;
      arReader.onloadend = function (oFRevent) {
        let data = oFRevent.target.result;
        JSZip.loadAsync(data).then((zip) => {
          let base = zip.file("output_step2/data.json").async("string");
              base.then(res => {
                let json = JSON.parse(res);
                _this.setAnalyzed(json);
              });
          _this.sepURL = []
          for(let i = 0;i < _this.sourcesNum; ++i){
            let unit = zip.file("output_step2/" + i + ".wav").async("blob");
              unit.then(res => {
                _this.setSepData(URL.createObjectURL(res), i, _this.sourcesNum);
              });
          }
        });
      }
    },
    setSepData(data, pos, sourcesNum) {
      let val = {"data": data, "pos": pos, "sourcesNum": sourcesNum};
      this.$emit("setSepData", val);
    },
    setAnalyzed(val){
      this.disAnalyze = true;
      this.loading = false;
      this.dialogFormVisible = false;
      this.$emit("setAnalyzed", val);
      this.jsonData = null;
    },
  },


}
</script>

<style scoped>
.xlargeWCss{
  margin-top: 8px;
  margin-left: 10px;
  width: 100%;
  float: left;
}
.nnarWCss{
  margin-right: 4px;
  width: 20%;
  float: left;
}
.narWCss{
  width: 20%;
  float: left;
}
.padWCss{
  padding-top: 7px;
  margin-right: 5px;
  width: 36%;
  float: left;
}

</style>