<template>
<div>
  <canvas ref="canvas"></canvas>
</div>
</template>

<script>
export default {
  name: "PianoCanvas",
  mounted() {
    this.cvs = this.$refs.canvas;
    this.ctx = this.$refs.canvas.getContext("2d");
  },
  methods:{
    setSize(w, h, padH){
      this.W = w;
      this.H = h;
      this.padH = padH;
      this.cvs.width = this.W;
      this.cvs.height = this.H;
    },
    getWhiteKeys(x){
      let register = Math.floor((x + 9) / 12);
      let octave = x + 9 - register * 12;
      if(x <= 6)
        return Math.floor((x + 2) / 2) + register * 7;
      else
        return Math.floor((x + 3) / 2)+ register * 7;
    },
    refresh(minPitch, maxPitch) {
      let ctx = this.ctx, h = this.H - this.padH;
      minPitch = 128 - maxPitch;
      maxPitch = 128 - minPitch;
      let onset = this.getWhiteKeys(minPitch);
      let offset = this.getWhiteKeys(maxPitch);
      let keys_num = offset - onset;
      let dt = h * 1. / keys_num;

      this.ctx.strokeStyle = "black";
      ctx.lineWidth = 1;
      let st = 0;
      for(let i = 0; i < keys_num; ++ i){
        ctx.strokeRect(0, i * dt + this.padH, this.W, dt);
      }
    }
  }
}
</script>

