<template>
  <div style="background-color: white">
    <canvas ref="canvas"
            ></canvas>
  </div>
</template>

<script>
import {copy} from '../../js/ultilities';
import {createCanvasData} from '../../js/CanvasData';

import { ElMessage } from 'element-plus'
export default {
  name: "BasicCanvas",
  props:{
    sources : Object,
    W : Number,
    H : Number,
    strokeSize: {
      type: Number,
      default :1,
    },
    brushSize: {
      type : Number,
      default : 0,
    },
    blockX: {
      type: Boolean,
      default: false,
    },
    blockY: {
      type: Boolean,
      default: false,
    }

  },
  data(){
    return {
      enableZoom : true,
      isGeneral : false,
      isCrop : false,
      isPaint : false,
      isDown : -1,
      selectTrk : 1,
      unlock : true,
      brushScale : 1,
    }
  },
  mounted() {
    this.ctx = this.$refs.canvas.getContext("2d");
    this.cvs = this.$refs.canvas;
    this.emptyCanvas(this.W, this.H, null);
    this.cvs.onmousedown = this.mouseDown;
    this.cvs.onmouseup = this.mouseUp;
    this.cvs.onmousemove = this.mouseMove;
    this.cvs.onMouseout = this.mouseOut;
    this.cvs.onmousewheel = this.mouseWheel;
  },
  methods:{
    callFunc(val){
      let func = val.func, arg = val.arg;
      this.$options.methods[func](this, arg);
    },

    setGeneral(_this, val){
      _this.isGeneral = true;
      _this.isCrop = false;
      _this.isPaint = false;
      _this.paint_all_data(1, "all");
    },
    setZoom(_this, val){
      _this.enableZoom = val;
    },

    cleanSelectArea(){
      let x, y, w, h;
      [x, y, w, h] = this.canvasData.selectedArea()
      if(w == 0 || h == 0)
        return;
      this.ctx.clearRect(x, y, w, h);
    },
    getMousePos(evt) {
      let cvs = this.cvs;
      let rect = cvs.getBoundingClientRect();
      return [
        (evt.clientX -rect.left) * cvs.width / rect.width,
        (evt.clientY -rect.top) * cvs.height / rect.height
      ];
    },

    emptyCanvas(w, h, val){
      let cvs = this.cvs;
      cvs.width = w;
      cvs.height = h;
      if(val == null)
        this.canvasData = null;
      else
        this.refreshData(val);
    },
    refreshData(val){
      this.canvasData = createCanvasData(val, this.cvs.width, this.cvs.height, this.blockX, this.blockY, this.brushSize);
      this.setGeneral(this);
      this.refresh();
    },
    paint(cleanFirst=0, mode="general"){
      if(cleanFirst == 1)
        this.ctx.clearRect(0, 0, this.cvs.width, this.cvs.height);
      else if(cleanFirst == 2)
        this.cleanSelectArea();
      this.canvasData.paint_all_data(mode, this.fill_rect);
    },

    fill_rect(px1, py, pxw, pyw, c, mode) {
      let ctx = this.ctx;
      ctx.fillStyle = c;
      let brushScale = this.canvasData.getScale();
      ctx.fillRect(px1, py, pxw + this.brushSize * brushScale[0], pyw + this.brushSize * brushScale[1]);
      if(mode == "select"){
        ctx.lineWidth = this.strokeSize;
        ctx.strokeStyle = "rgb(87,87,87)";
        ctx.strokeRect(px1, py, pxw + this.brushSize * brushScale[0], pyw + this.brushSize * brushScale[1]);
      }
      if(mode == "activate"){
        ctx.fillStyle = "rgba(0,0,0,0.8)";
        ctx.fillRect(px1, py, pxw + this.brushSize * brushScale[0], pyw + this.brushSize * brushScale[1]);
      }
    },

    mouseWheel(e){
      if(this.canvasData == null){
        return;
      }
      let direction = e.deltaY > 0 ? 'down':'up';
      //let dt = this.brushSize > 0 ? 0.2 / this.brushSize: 0;
      if (direction=='up' && e.deltaY <= -5){
        let pos = this.getMousePos(e);

        this.canvasData.scaleView(0.9, pos)
        this.paint_all_data(1, "all");
        this.mouseMoveAction();

      }else if(direction=='down' && e.deltaY >= 125){
        let pos = this.getMousePos(e);
        this.canvasData.scaleView(1.1, pos);
        this.paint_all_data(1, "all");
        this.mouseMoveAction();
      }
      e.preventDefault();
    },
    mouseDown(evt){
      this.isDown = evt.button;
      if(this.canvasData == null || this.isDown != 0)
        return;
      this.paint_all_data(2, "highlight");
      this.mouseDownAction();
      let pos = this.getMousePos(evt);
      this.canvasData.setSelectedArea(pos, pos);
      if(this.isGeneral)
        this.canvasData.backupStateDataBorder();
      evt.preventDefault();
    },
    mouseDownAction(){
    },
    mouseOut(evt){
      this.mouseUp(evt);
    },
    mouseUp(evt){
      if (this.isDown > -1) {
        this.isDown = -1;
      }
      evt.preventDefault();
    },
    mouseMove(evt){
      if(this.canvasData == null || this.isDown != 0)
        return;
      let newCenter = this.getMousePos(evt)
      if (this.isGeneral) {
        this.canvasData.moveView(newCenter);
        this.paint_all_data(1, "all");
        this.mouseMoveAction();
      }else if (this.unlock){
        this.unlock = false;
        this.paint_all_data(2, "highlight");
        this.canvasData.setSelectedArea(null, newCenter);
        this.ctx.fillStyle = "rgba(0,255,196,0.22)";
        let x, y, w, h;
        [x, y, w, h] = this.canvasData.selectedArea();
        if(w > 4 && h > 4){
          this.ctx.fillRect(x + 2, y + 2, w - 2, h - 2);
          this.paint_all_data(0, "highlight");
        }
        let c = this.canvasData.filter()
        this.timbreSpaceAction("updateColor", c);
        this.unlock = true;
      }
      evt.preventDefault();
    },
    mouseMoveAction(){

    },
  },
}
</script>

<style lang="scss">
.canvasAreaCSS{
  position: relative;
  //background-color: rgba(239, 238, 238, 0.99);
  //box-shadow: 0 0 3px 3px #666;
}
</style>

