
function copy(x){
    return JSON.parse(JSON.stringify(x));
}
function swap(x, y){
      let t = x;
      x = y;
      y = t;
      return [x, y];
    }

function formatRect(p1, p2){
    let x = p1[0], y = p1[1];
    let w = p2[0] - x, h = p2[1] - y;
    if(w < 0){
        w = -w;
        x = p2[0];
    }
    if(h < 0){
        h = -h;
        y = p2[1];
    }
    return [x, y, w, h];
}
function encode( s ) {
	let out = [];
	for ( let i = 0; i < s.length; i++ ) {
		out[i] = s.charCodeAt(i);
	}
	return new Uint8Array( out );
}
function formatTime(x, float=0){
    let min = Math.floor(x / 60);
    let sec = x - min * 60;
    sec = float>0?sec.toFixed(float):Math.floor(sec);
    min = (min + "").padStart(2, "0");
    sec = (sec + "").padStart(2, "0");

    min = min.slice(-2, min.length);
    if(float == 0){
        sec = sec.slice(-2, sec.length);
    }else{
        sec = sec.slice(-3 - float, sec.length);
    }
    return min + ":" + sec;
}

export {copy, formatRect, swap, formatTime, encode}