<template>
  <div class="xlargeWCss">
    <div class="largePadWCss">
      <span>Upload a mixture:</span>
      <div class="narWCss">
            mp3/wav file with a duration less than 2 min
      </div>
    </div>
    <el-upload
      ref="uploadRef"
      action=""
      :limit="1"
      :on-exceed="handleExceed"
      :on-change="onChangeFile"
      :auto-upload="false">
        <el-button type="primary"  size="small">Select</el-button>
      </el-upload>
    </div>

  <div class="audioPlayer">
    <div class="xlargeWCss">
    <div class="narCss">
      <span>Audio play mode:</span>
    </div>
    <div class="largeWCss">
      <el-radio-group v-model="mode" @change="onChangeMode">
          <el-radio :label="1">Mixture</el-radio>
          <el-radio :label="2" :disabled="!analyzed">Target sources</el-radio>
      </el-radio-group>
    </div>
  </div>
    <div class="xlargeWCss" >
      <audio id="myAudio" controls :src="audioSrc" @timeupdate="updateTime"></audio>
    </div>
</div>
</template>

<script>
export default {
  name: "AudioPanel",
  data(){
    let current = 0, total = 0;
    return{
      sepURL : {},
      mode: 1,
      selectTrk : 1,
      enableAudioSubmit : false,
      analyzed : false,
      audioSrc : "mix.wav",
      hasRawInput : false,
      hasSeparate : false,
      minDur : current,
      maxDur : total,
      currentDur : current,
      totalDur : total,
      processStr : this.getFormatProcess(current, total),
    }
  },


  watch:{
    currentCur(){
      this.processStr = this.getFormatProcess();
    }
  },
  mounted() {
    this.audio = document.getElementById("myAudio");
  },
  methods: {
    padNum(x, n) {
      return (Array(n).join("0") + x).slice(-n);
    },
    formatTime(s) {
      let m = Math.floor(s / 60);
      s = s - m * 60;
      return this.padNum(m, 2) + " : " + this.padNum(s, 2);
    },
    getFormatProcess(a, b) {
      if (a == null) {
        a = this.currentDur;
        b = this.totalDur;
      }
      return this.formatTime(a) + " - " + this.formatTime(b);
    },

    updateTime(e){
      this.currentDur = e.target.currentTime;
      this.$emit("updatePlayingBar", this.currentDur);
    },
    onChangeFile(file) {
      file = file.raw;
      let isAudio = (file.type === 'audio/mp3' || file.type === 'audio/wav');
      let valiSize = file.size / 1024 / 1024 < 20;
      if (!isAudio) {
        this.$message.error("Please select a file with wav/mp3 format ()");
      }
      if (!valiSize) {
        this.$message.error('Refuse! it\'s tooooo large ()');
      }
      if (isAudio && valiSize) {
        let _this = this
        let urlReader = new FileReader();
        urlReader.readAsDataURL(file);
        this.audioData = {"data" : file, "type" : file.type};
        this.$emit("setAudioData", this.audioData);
        urlReader.onloadend = function (oFRevent) {
          _this.mixtureURL = oFRevent.target.result;
          _this.audioSrc = oFRevent.target.result;
          _this.setAnalyze();
        };
      }else{
        this.$refs.uploadRef.clearFiles()
      }
    },
    onClickUpload(evt) {
      this.$refs.uploadRef.submit();
    },
    onClickPlay() {
      this.$refs.audio.play();
    },
    onChangeMode(){
      let res = this.mode == 1 ? this.mixtureURL : this.sepURL[""+(this.selectTrk - 1)];
      this.changeAudioSrc(res);
    },
    changeAudioSrc(res){
      let audio = this.audio;
      let currentTime = audio.currentTime;
      audio.pause();
      this.audioSrc = res;
      audio.onloadedmetadata = function() {
        audio.currentTime = currentTime;
        audio.play();
      };
    },
    setSepData(val){
      console.log(val.pos, val.data);
      this.sepURL[val.pos + ""] = val.data;
    },
    setSelectTrk(val){
      this.selectTrk = val;
      if(this.mode == 2){
        let res = this.sepURL[""+(this.selectTrk - 1)];
        this.changeAudioSrc(res);
      }
    },
    setAnalyze(){
      this.hasRawInput = true;
      this.analyzed = false;
      this.mode = 1;
      this.audio.currentTime = 0;
      this.$emit("setAnalyze", null);
    },
    handleExceed(files){
      this.$refs.uploadRef.clearFiles()
      this.$refs.uploadRef.handleStart(files[0])
    },
    setAnalyzed(val){
      if(!val){
        this.mode = 1;
      }
      this.analyzed = val;
    }
  }
}
</script>

<style scoped>
.audioPlayer{
  margin-top: 15px;
  padding-top: 10px;
  margin-bottom: 10px;
  margin-left: 5px;
  width: 96%;
  background-color: #b4ccef;
  height: 45%;
  float: left;
}
.xlargeWCss{
  margin-top: 10px;
  margin-left: 10px;
  width: 100%;
  float: left;
}
.narCss{
  width: 35%;
  float: left;
  padding-left: 0;
  padding-right: 10px;
}

.largeWCss{
  width: 60%;
  float: left;
}
.largePadWCss{
  padding-top: 5px;
  margin-left: 12px;
  margin-right: 2px;
  width: 60%;
  float: left;
}
.narWCss{
  padding-left: 10px;
  padding-top: 10px;
  width: 90%;
  font-size: 10pt;
  color: gray;
}
</style>