class TimbreDB {
    constructor(pca, center) {
        this.mean = pca.mean;
        this.cps = pca.cps;
        this.centerEmb = center;
        this.initialize();
    }
    initialize() {
        let centerData = this.pcaTransform(this.centerEmb);
        let tags = []
        for(let i = 0; i < centerData.length; ++i)
            tags.push((i + 1) + "");
        let centerTags = tags;
        this.centerData = {"data" : centerData, "tag" : centerTags};
    }

    pcaTransform(x){
        let y = [];
        for(let i = 0; i < x.length ; ++ i){
            let res = []
            for(let j = 0; j < this.cps[0].length; ++ j){
                let val = 0.;
                for(let k = 0; k < this.cps.length; ++ k){
                    val += (x[i][k] - this.mean[k]) * this.cps[k][j];
                }
                res.push(val);
            }
            y.push(res);
        }
        return y;
    }
    getCenters(){
        return this.centerData;
    }
}

function createTimbreDB(pca, center){
    let timbreDB = new TimbreDB(pca, center);
    return timbreDB;
}

export {createTimbreDB}