<template>
<div class="pianoPanel">
  <div class="basicPanel">
    <div class="xlargeWCss">
      Mouse Settings:
    </div>
    <div class="xlargeWCss">
      &nbsp; - Click
    </div>
    <div class="xlargeWCss">
      <el-radio-group v-model="mode" @change="onChangeMode">
        <div class="xlargeWCss">
          <el-radio :label="1">Drag</el-radio>
        </div>
        <div class="xlargeWCss">
          <el-radio :label="2">Crop</el-radio>
        </div>
        <div class="xlargeWCss">
          <el-radio :label="3">Fill</el-radio>
        </div>
      </el-radio-group>
    </div>
    <div class="xlargeWCss">
      &nbsp; - Scroll
    </div>
    <div class="xlargeWCss">
      <el-checkbox v-model="enableZoom" @change="onChangeZoom">zoom</el-checkbox>
    </div>
  </div>
  <div class="cropPanel">
    <audio-panel ref="audioPanel" @setAnalyze="setAnalyze"
                 @setAudioData="setAudioData"
                  @updatePlayingBar="updatePlayingBar"></audio-panel>
  </div>
  <div class="cropPanel">
    <transfer-data-dialog ref="transferDataDialog" :sources="allSources"
                          @setAnalyzed="setAnalyzed"
                          @setRepData="setRepData"
                          @setSepData="setSepData"></transfer-data-dialog>

    <div class="xlargeWCss">
      <div class="padWCss"><span>Target source:</span></div>
      <div class="largeWCss">
        <el-select v-model="selectTrk" placeholder="Select source" size="small" :disabled="!analyzed" @change="onSelectChangeTrk">
          <el-option  v-for="item in allSources" :key="item.id" :label="item.label" :value="item.id" />
        </el-select>
      </div>
    </div>
    <div class="xlargeWCss">
      <div class="padWCss"><span>Start - End time (s):</span></div>
      <div class="mlargeWCss">
        <div class="timeCss"><el-input v-model="minSec" disabled placeholder="00:00" /></div>
        <div class="narWCss">-</div>
        <div class="timeCss"><el-input v-model="maxSec" disabled placeholder="00:00" /></div>
  <!--      <el-slider class="slider" v-model="cropAreaX" range :max="cropAreaXMax" :min="cropAreaXMin" size="small"
                   :disabled="!analyzed"
                   @change="onChangeCrop"/>-->
      </div>
    </div>
    <div class="xlargeWCss">
        <el-button type="warning"   size="small" @click="onClickCrop" :disabled="!isCrop">Crop</el-button>
        <el-button type="primary" plain size="small" @click="onClickRevise" :disabled="!isCrop">Revise</el-button>
        <el-button type="warning"  size="small" @click="onClickErase" :disabled="!isCrop">Erase</el-button>
        <el-button type="primary" plain size="small" @click="onClickFill" :disabled="!isPaint">Fill</el-button>
      </div>
    <div class="xlargeWCss">
        <el-button type="warning" plain size="small" @click="onClickUndo" :disabled="!isCrop">Undo</el-button>
        <el-button type="primary"  size="small" @click="onClickRedo" :disabled="!isCrop">Redo</el-button>
        <el-button type="warning" plain size="small" @click="onClickReset" :disabled="!isCrop">Reset</el-button>
      <el-button type="danger"  size="small" :disabled="!isCrop" @click="onClickRefine">Refine</el-button>
      <save-change-dialog ref="saveChangeDialog" @setAnalyzed="setAnalyzed"
                          @setSepData="setSepData"></save-change-dialog>

      </div>
    </div>

</div>
</template>

<script>

import AudioPanel from "@/components/panel/AudioPanel"
import TransferDataDialog from "@/components/dialog/TransferDataDialog";
import SaveChangeDialog from "@/components/dialog/SaveChangeDialog";
import {formatTime} from "../../js/ultilities"

export default {
  name: "PianoPanel",
  components: {
    SaveChangeDialog,
    TransferDataDialog,
    AudioPanel,
  },
  props :{
    sources : Object,
  },
  data(){

    return{
      mode : 1,
      enableZoom : true,
      isSelect: false,
      analyzed: false,
      dialogFormVisible : false,
      resUrl : "",
      formLabelWidth : 120,
      isDrag: false,
      isCrop: false,
      isPaint: false,
      minSec : "00:00",
      maxSec : "00:00",
      selectTrk : 1,
      allSources : this.sources,
    }
  },
  methods:{
    callFunc(val){
      let func = val.func, arg = val.arg;
      this.$options.methods[func](this, arg);
    },
    setAnalyze(){
      this.analyzed = false;
      this.$emit("refreshCanvas", null);
      this.$refs.transferDataDialog.setAnalyze();
      this.$refs.audioPanel.setAnalyzed(this.analyzed);
      this.reset();
      let analyzed = {"analyzed" : this.analyzed, "sources" : this.allSources, "selectTrk" : this.selectTrk};
      this.$emit("timbrePanelAction", {"func" : "setAnalyzed", "arg" : analyzed});
    },
    setRepData(val){
      this.$refs.saveChangeDialog.setRepData(val);
    },
    setSepData(val){
      this.$refs.audioPanel.setSepData(val);
    },
    setAnalyzed(val){
      this.$emit("refreshCanvas", val);
      this.allSources = []
      for(let i = 0; i<this.$refs.transferDataDialog.sourcesNum; ++i) {
        this.allSources.push({"id" : i + 1, "label" : (i + 1) + ""});
      }
      this.selectTrk = 1;
      if(val == null){
        this.analyzed = false;
      }else{
        this.analyzed = true;
      }
      this.$refs.audioPanel.setAnalyzed(this.analyzed);
      let analyzed = {"analyzed" : this.analyzed, "sources" : this.allSources, "selectTrk" : this.selectTrk, "pca" : val.pca, "center" : val.center}
      this.$emit("timbrePanelAction", {"func" : "setAnalyzed", "arg" : analyzed});

    },
    pianoRollAction(func, arg=null){
      let val = {"func" : func, "arg" : arg};
      this.$emit("pianoRollAction", val);
    },
    timbreSpaceAction(func, arg=null){
      let val = {"func" : func, "arg" : arg};
      this.$emit("timbreSpaceAction", val);
    },

    updatePlayingBar(val){
      this.pianoRollAction("updatePlayingBar", val);
    },
    reset(){
      if(!this.analyzed){
        this.isDrag = false;
        this.isCrop = false;
        this.isPaint = false;
        return true;
      }
      return false;
    },
    onSetDrag(evt){
      if(this.reset())
        return;
      this.isDrag = true;
      this.isCrop = false;
      this.isPaint = false;
      this.pianoRollAction("setGeneral");
    },
    onSetPaint(evt){
      if(this.reset())
        return;
      this.isDrag = false;
      this.isCrop = false;
      this.isPaint = true;
      this.pianoRollAction("setPaint");
    },
    onSetCrop(evt){
      if(this.reset())
        return;
      this.isDrag = false;
      this.isCrop = true;
      this.isPaint = false;
      this.pianoRollAction("setCrop");
    },
    onChangeZoom(evt){
      this.pianoRollAction("setZoom", this.enableZoom);
      this.timbreSpaceAction("setZoom", this.enableZoom);
    },
    onChangeMode(evt){
      if(this.mode == 1)
        this.onSetDrag(evt);
      else if(this.mode == 2)
        this.onSetCrop(evt);
      else if(this.mode == 3)
        this.onSetPaint(evt);
    },
    onSelectChangeTrk(evt){
      this.pianoRollAction("selectChangeTrk", this.selectTrk);
      this.$refs.audioPanel.setSelectTrk(this.selectTrk);
    },
    updateSecRange(_this, arg){
      _this.minSec = formatTime(arg[0]);
      _this.maxSec = formatTime(arg[2]);
      _this.$emit("updateCanvas", arg);
    },

    onClickCrop(evt){
      this.pianoRollAction("crop");
    },
    onClickRevise(evt){
      this.pianoRollAction("revise");
    },
    onClickErase(evt){
      this.pianoRollAction("erase");
    },
    onClickFill(evt){
      this.pianoRollAction("fill");
    },
    onClickUndo(evt){
      this.pianoRollAction("undo");
    },
    onClickRedo(evt){
      this.pianoRollAction("redo");
    },
    onClickReset(evt){
      this.pianoRollAction("reset");
    },
    onClickRefine(evt){
      this.pianoRollAction("setJsonData", null);
      this.$refs.saveChangeDialog.showDialog();
    },

    setJsonData(_this, val){
      _this.$refs.saveChangeDialog.setJsonData(val);
    },
    setAudioData(val){
      this.$refs.transferDataDialog.setAudioData(val);
    },
  }
}
</script>

<style lang="scss">
.switch{
    --el-switch-on-color: #174D96FF !important;
}
.slider {
  .el-slider__bar{
    --el-slider-main-background-color: #174D96FF;
    }
  .el-slider__button{
    --el-slider-main-background-color: #174D96FF;
    }
}
</style>
<style lang="scss" scoped>
.pianoPanel{
  padding: 5px;
  width: 100%;
  height: 240px;
}
.basicPanel{
  float: left;
  width: 16%;
  height: 100%;
  background-color: #d3e5f6;
}
.cropPanel{
  margin-left: 10px;
  float: left;
  width: 40%;
  height: 100%;
  background-color: #d3e5f6;
}
.paintPanel{
  float: left;
  width: 30%;
  height: 100%;
  margin-left: 10px;
  background-color: #d3e5f6;
}

.xlargeWCss{
  margin-top: 8px;
  margin-left: 10px;
  width: 100%;
  float: left;
}
.mlargeWCss{
  margin-left: 20px;
  width: 50%;
  float: left;
}
.narCss{
  //width: 80%;
  width: 40%;
  float: left;
  padding-left: 0;
  padding-right: 10px;
}
.largeWCss{
  width: 60%;
  float: left;
}
.nnarWCss{
  margin-right: 4px;
  width: 20%;
  float: left;
}
.narWCss{
  width: 15%;
  float: left;
  padding-left: 5%;
  font-size: 20pt;
}
.midWCss{
  margin-right: 5px;
  width: 30%;
  float: left;
}
.padWCss{
  padding-top: 7px;
  margin-right: 5px;
  width: 36%;
  float: left;
}
.padNarWCss{
  padding-top: 7px;
  margin-right: 5px;
  width: 20%;
  display: inline;
  float: left;

}
.largeBandCss{
  margin-top: 10px;
  margin-left: 20px;
  width: 100%;
  height: 100px;

}
.narrowBandCss{
  margin-top: 5px;
  margin-left: 20px;
  width: 100%;
  height: 40px;
}
.hintCss{
  width: 80%;
  height: 10%;
  margin-left: 15%;
  margin-bottom: 4%;
}
.timeCss{
  width: 40%;
  float: left;
}

</style>