<script>
import BasicCanvas from "@/components/canvas/BasicCanvas";

export default {
  extends: BasicCanvas,
  name: "PianoRollCanvas",
  mounted() {

  },
  methods:{
    mouseDownAction(){
      this.updateTimbreSpace();
    },
    mouseMoveAction(){
      this.updateSecRange();
    },
    refresh(){
      let pitchRange = this.canvasData.getYRange(129, -1);
      this.timbrePanelAction("setPitchRange", pitchRange);
      this.updateSecRange();
    },
    paint_all_data(cleanFirst=0, mode="general"){
      this.paint(cleanFirst, mode);
    },
    pianoPanelAction(func, arg){
      let val = {"func" : func, "arg" : arg};
      this.$emit("pianoPanelAction", val);
    },
    timbrePanelAction(func, arg){
      let val = {"func" : func, "arg" : arg};
      this.$emit("timbrePanelAction", val);
    },
    timbreSpaceAction(func, arg){
      let val = {"func" : func, "arg" : arg};
      this.$emit("timbreSpaceAction", val);
    },
    updateTimbreSpace(){
      this.timbreSpaceAction("updateColor", this.canvasData.paintedColor);
    },
    setJsonData(_this){
      let val = {"data" : _this.canvasData.paintedData, "color": _this.canvasData.paintedColor};
      _this.pianoPanelAction("setJsonData", val);
    },
    crop(_this){
      _this.canvasData.crop();
      _this.paint_all_data(1, "all");
      _this.updateTimbreSpace();
      _this.updateSecRange();
    },
    reset(_this){
      _this.canvasData.reset();
      _this.paint_all_data(1, "all");
      _this.updateTimbreSpace();
      _this.updateSecRange();
    },
    fill(_this){
      _this.replace(_this.selectTrk + 1, true);
      _this.paint_all_data(1, "all");
      _this.updateTimbreSpace();
    },
    revise(_this){
      _this.canvasData.revise();
      _this.paint_all_data(1, "all");
      _this.updateTimbreSpace();
    },
    erase(_this){
      _this.canvasData.erase();
      _this.paint_all_data(1, "all");
      _this.updateTimbreSpace();
    },
    updatePlayingBar(_this, sec){
      if(_this.canvasData == null)
        return;
      let pos = _this.canvasData.getBarPos(Math.round(sec * 62.5));
      _this.paint_all_data(1, "all");
      _this.ctx.fillStyle = "rgba(0,255,196,0.22)";
      _this.ctx.fillRect(pos, 0, 5, _this.cvs.height);
      _this.updateSecRange();
    },
    selectChangeTrk(_this, trk){
      _this.canvasData.setSelectedTrk(trk);
      _this.paint_all_data(1, "all");
      _this.timbreSpaceAction("changeTrk", trk)
      _this.updateTimbreSpace();
    },
    updateSecRange(){
      let args = this.canvasData.getXViewBorder();
      this.pianoPanelAction("updateSecRange", args);
    },

    undo(_this){
      _this.canvasData.undo();
      _this.paint_all_data(1, "all");
      _this.updateTimbreSpace();
    },
    redo(_this){
      _this.canvasData.redo();
      _this.paint_all_data(1, "all");
      _this.updateTimbreSpace();
    },
    setPaint(_this, val){
      _this.isGeneral = false;
      _this.isCrop = false;
      _this.isPaint = true;
      _this.paint_all_data(1, "all");
    },
    setCrop(_this){
      _this.isGeneral = false;
      _this.isCrop = true;
      _this.isPaint = false;
      _this.paint_all_data(1, "all");
    },


  }
}
</script>

