<template>
<div>
  <canvas ref="canvas"></canvas>
</div>
</template>

<script>
import {formatTime} from '../../js/ultilities';

export default {
  name: "TimeTickerCanvas",
  mounted() {
    this.cvs = this.$refs.canvas;
    this.ctx = this.$refs.canvas.getContext("2d");
  },
  methods:{
    setSize(w, h){
      this.W = w;
      this.H = h;
      this.cvs.width = this.W;
      this.cvs.height = this.H;
    },
    formatSec(minSc, maxSc){
      let dt = 1, float=0;
      if(maxSc - minSc <= 30 && maxSc - minSc > 10){
        dt = 2;
        float = 0;
      }else if(maxSc - minSc <= 10 && maxSc - minSc > 5){
        dt = 10;
        float = 0;
      }else if(maxSc - minSc <= 5){
        dt = 20;
        float = 1;
      }
      maxSc = maxSc * dt;
      minSc = minSc * dt;
      return [minSc, maxSc, dt, float];
    },
    refresh(minSec, maxSec){
      let ctx = this.ctx;
      let MB = 10, scale, float;
      ctx.clearRect(0, 0, this.W, this.H);
      ctx.lineWidth = 1;
      ctx.strokeStyle = "#000000";
      [minSec, maxSec, scale, float] = this.formatSec(minSec, maxSec);
      let dt = this.W / (maxSec - minSec)
      for(let i = 0; i <= maxSec - minSec; ++i){
        ctx.beginPath();
        ctx.moveTo(i * dt,0);
        if(Math.floor((minSec + i) / MB) * MB == minSec + i){
          ctx.lineTo(i * dt, this.H);
          ctx.stroke();
          ctx.fillText(formatTime((minSec + i)*1./scale, float), (i + 0.1) * dt, this.H)
        }
        else{
          ctx.lineTo(i * dt,this.H *0.4);
          ctx.stroke();
        }



      }
    }

  }
}
</script>

