<template>
<div class="timbrePanelCss">
    <div class="narrowBandCss"><span>Timbre database: </span></div>
    <div class="hintCss">
      <div><b>Load</b> timbre embeddings from a database</div>
      <div><b>Tag</b> sources using timbre embeddings in the database</div>
      <div><b>Select</b> timbre embeddings to re-separate</div>
      <div><b>Save</b> current timbre embeddings</div>
    </div>
    <div class="largeBandCss">
      <el-button type="primary"  size="small" :disabled="!analyzed">Load</el-button>
      <el-button type="primary" plain size="small" :disabled="!analyzed">Tag</el-button>
      <el-button type="warning"  size="small" :disabled="!analyzed">Select</el-button>

  </div>
  <div class="largeBandCss">
    <div class="largeWCss">
      <el-input :disabled="!analyzed"
        v-model="instr"
        placeholder="Instrument name"
        class="input-with-select">
        <template #prepend>
        <el-select v-model="selectTrk" @change="onSelectChangeTrk" placeholder="Select" style="width: 110px" :disabled="!analyzed">
          <el-option  v-for="item in allSources" :key="item.id" :label="item.label" :value="item.id" />
        </el-select>
      </template>
      </el-input>
    </div>
    <div class="narrowCss"><el-button type="danger" size="small" :disabled="!analyzed">Save</el-button></div>
  </div>
  <div class="largeBandCss">
    <div class="narrowWCss"><span>Pitch range: </span></div>
    <div class="midWCss">
      <el-slider v-model="pitchRange" range :max="pitchMax" :min="pitchMin" size="small" :disabled="!analyzed"
                  class="slider" @change="onChangePitchRange"/>
    </div>
  </div>

</div>
</template>

<script>

export default {
  name: "TimbrePanel",
  components: {
  },
  props :{
    sources : Object,
  },
  data(){
    return{
      analyzed : false,
      instr : "undefined",
      pitchRange : [1, 128],
      pitchMin : 1,
      pitchMax : 128,
      selectTrk : 1,
      allSources: this.sources,

    }
  },
  methods:{
    callFunc(val){
      let func = val.func, arg = val.arg;
      this.$options.methods[func](this, arg);
    },
    setPitchRange(_this, val){
      _this.pitchMin = val[0];
      _this.pitchMax = val[1];
      _this.pitchRange = val;
    },
    setAnalyzed(_this, val){
      _this.analyzed = val.analyzed;
      _this.allSources = val.allSources;
      _this.selectTrk = val.selectTrk;
    },
    pianoRollAction(func, arg=null){
      let val = {"func" : func, "arg" : arg};
      this.$emit("pianoRollAction", val);
    },
    timbreSpaceAction(func, arg=null){
      let val = {"func" : func, "arg" : arg};
      this.$emit("timbreSpaceAction", val);
    },
    onChangePitchRange(evt){
      this.timbreSpaceAction("filter", [129 - this.pitchRange[1], 129 - this.pitchRange[0]]);
    },
    onSelectChangeTrk(evt){
      this.pianoRollAction("selectChangeTrk", this.selectTrk);
      this.timbreSpaceAction("selectChangeTrk", this.selectTrk);
    },

  }
}
</script>

<style lang="scss" scoped>

.timbrePanelCss{
  width: 100%;
  margin-left: 5px;
}
.largeBandCss{
  width: 100%;
  float: left;
  margin-top: 10px;
  margin-left: 20px;
}
.narrowBandCss{
  margin: 10px;
  width: 100%;
  float: left;
}
.hintCss{
  margin-left: 20px;
  width: 100%;
  float: left;
}

.midWCss{
  width: 50%;
  float: left;
}
.narrowWCss{
  padding-top: 6px;
  width: 25%;
  float: left;
}
.largeWCss{
  width: 60%;
  float: left;
}
.narrowCss{
  margin-top: 5px;
  margin-left: 5px;
  width: 30%;
  height: 20px;
  float: left;
}



</style>