import {copy, formatRect, swap} from "@/js/ultilities";
import {ElMessage} from "element-plus";

class CanvasData{
    constructor(val, canvasW, canvasH, blockX, blockY, brushSize){
        this.brushSize = brushSize;
        this.blockX = blockX;
        this.blockY = blockY;
        this.paintedData = val.data.data;
        this.paintedColor =val.color;
        this.border = val.data.border
        this.canvasW = canvasW;
        this.canvasH = canvasH;
        this.initialize();
    }
    initialize(){
        this.initColors();
        this.initBorder();
        this.initProps();
        this.pushInStack();
    }
    initColors(){
        let darkColors = ["rgb(194,194,194)", "rgb(255,202,202)",
        "rgb(172,212,255)", "rgb(252,243,154)", "rgb(158,255,152)",
        "rgb(210,164,252)", "rgb(252,187,122)"];
        let lightColors = ["rgb(121,121,121)", "rgb(255,0,0)",
        "rgb(0,124,255)", "rgb(252,228,0)", "rgb(13,243,0)",
        "rgb(130,0,248)", "rgb(255,132,0)"];
        let alphaColors = ["rgba(75,75,75,0.63)", "rgba(150,0,0,0.74)",
        "rgba(0,67,141,0.77)", "rgba(148,138,0,0.7)", "rgba(3,141,0,0.68)",
        "rgba(70,0,140,0.74)", "rgba(140,70,0,0.68)"];
        this.lightColors = lightColors;
        this.darkColors = darkColors;
        this.alphaColors = alphaColors;
    }
    initBorder(){
        this.selectedTrk = 1;
        this.dataNum = this.paintedData.length;
        this.viewDataBorder = copy(this.border);
        this.stateDataBorder = copy(this.border);
        this.clickCenter = [0, 0];
        this.currentCenter = [this.canvasW, this.canvasH];
    }
    initProps(){
        this.dataStack = [];
        this.stackCur = 0;
        this.xlimit = 63;
        this.ylimit = 1;
    }
    updateColor(c){
        this.paintedColor = c;
    }
    setSparseView(){
        this.darkColors[0] = "rgba(255,255,255,0)";
    }
    setSelectedTrk(val){
        this.selectedTrk = val;
    }
    hasNoData(){
        return this.paintedData == null;
    }
    pos2data(x, y, state=false){
      if(state){
        x = x / this.canvasW * (this.stateDataBorder[2] - this.stateDataBorder[0]) + this.stateDataBorder[0];
        y = y / this.canvasH * (this.stateDataBorder[3] - this.stateDataBorder[1]) + this.stateDataBorder[1];
      }else{
        x = x / this.canvasW * (this.viewDataBorder[2] - this.viewDataBorder[0]) + this.viewDataBorder[0];
        y = y / this.canvasH * (this.viewDataBorder[3] - this.viewDataBorder[1]) + this.viewDataBorder[1];
      }
      return [x, y];
    }
    data2pos(x, y){
      x = (x - this.viewDataBorder[0]) / (this.viewDataBorder[2] - this.viewDataBorder[0]) * this.canvasW;
      y = (y - this.viewDataBorder[1]) / (this.viewDataBorder[3] - this.viewDataBorder[1]) * this.canvasH;
      return [x, y];
    }
    formatData(p){
      let x = p[0], y = p[1];
      x = x * (this.border[2] - this.border[0]) / this.canvasW;
      y = y * (this.border[3] - this.border[1]) / this.canvasH;
      x = Math.round(x) / (this.border[2] - this.border[0]) * this.canvasW;
      y = Math.round(y) / (this.border[3] - this.border[1]) * this.canvasH;
      return [x, y];
    }
    formatViewBorderUnit(i, block=true){
      let scale = (this.border[2 + i] - this.border[i]) / (this.viewDataBorder[2 + i] - this.viewDataBorder[i]);
      if(scale < 1){
        this.viewDataBorder[i] = this.border[i];
        this.viewDataBorder[2 + i] = this.border[2 + i];
      }
      if (this.viewDataBorder[i] < this.border[i]){
          if(block)
            this.viewDataBorder[2 + i] += this.border[i] - this.viewDataBorder[i];
        this.viewDataBorder[i] = this.border[i];
      } else if (this.viewDataBorder[2 + i] > this.border[2 + i]){
          if(block)
            this.viewDataBorder[i] -= (this.viewDataBorder[2 + i] - this.border[2 + i]);
        this.viewDataBorder[2 + i] = this.border[2 + i];
      }
    }
    formatViewBorder(block=true){
      this.formatViewBorderUnit(0, block);
      this.formatViewBorderUnit(1, block);
    }
    selectedData(){
      let x, y, w, h, x1, x2, y1, y2;
      [x, y, w, h] = formatRect(this.clickCenter, this.currentCenter);
      [x1, y1] = this.pos2data(x, y);
      [x2, y2] = this.pos2data(x + w, y + h);
      x1 = Math.round(x1);
      x2 = Math.round(x2);
      y1 = Math.round(y1);
      y2 = Math.round(y2);
      return [x1, y1, x2, y2];
    }
    selectedArea(){
        let x1, x2, y1, y2;
        [x1, y1, x2, y2] = this.selectedData();
        [x1, y1] = this.data2pos(x1, y1);
        [x2, y2] = this.data2pos(x2, y2);
        return [x1, y1, x2 - x1, y2 - y1];
    }
    resetSelectedArea(){
      this.clickCenter = [0, 0];
      this.currentCenter = [this.canvasW, this.canvasH];
    }
    resetViewBorder(){
      this.viewDataBorder = copy(this.border);
    }
    setSelectedArea(pos1, pos2){
        if(pos1 != null)
            this.clickCenter = pos1;
        if(pos2 != null)
            this.currentCenter = pos2;
    }
    paint_all_data(mode, fill_rect){
      let x, y, w, h, ind, x1, y1, x2, y2;
      if(mode == "all"){
          x = 0;
          y = 0;
          w = this.canvasW;
          h = this.canvasH;
          mode = "general";
      } else
         [x, y, w, h] = this.selectedArea();
      if(w == 0 || h == 0)
        return;
      [x1, y1] = this.pos2data(x, y);
      [x2, y2] = this.pos2data(x + w, y + h);
      ind = 0;
      while(ind != - 1){
        ind = this.paint_data_unit(ind, x1, y1, x2, y2, mode, fill_rect);
      }
    }
    paint_data_unit(index, x1, y1, x2, y2, mode, fill_rect){
      let data = this.paintedData;
      let c = this.paintedColor[index];
      let activate = c - 1 == this.selectedTrk;
      c = activate ? this.lightColors[c - 1] : this.darkColors[c - 1];

      let pre = index, next = data[index][2];
      if(data[index][0] > x2 )
        return next;
      if(data[index][1]> y2)
        return next;
      if(data[index][1] < y1)
        return next;

      let color = this.paintedColor, skip = -1;
      while(next > -1 && data[pre][1] == data[next][1] && data[pre][0] + 1 >= data[next][0]){
        if(color[next] != color[index]) {
          while (next > -1 && color[next] != color[index]) {
              if(skip == -1)
                  skip = next;
              next = data[next][2];
          }
        }else{
          pre = next
          next = data[pre][2];
        }
      }
      if(data[pre][0] < x1)
        return skip > 0 ?skip : next;

      if(activate)
        mode = "select";

      let dt = this.brushSize > 0 ? 0 : 1;
      let pyw = this.brushSize > 0? this.brushSize : this.canvasH / (this.viewDataBorder[3] - this.viewDataBorder[1]);
      if(mode == "background"){
        c = this.darkColors[0];
      }
      let px1, px2, py, pxw;
      [px1, py] = this.data2pos(data[index][0], data[index][1]);
      [px2, py] = this.data2pos(data[pre][0] + dt, data[index][1]);
      pxw = px2 - px1;
      fill_rect(px1, py, pxw, pyw, c, mode);
      return skip > 0 ?skip : next;
    }

    backupStateDataBorder(){
        this.stateDataBorder = copy(this.viewDataBorder);
    }
    getYRange(bound, sign){
        return [bound + sign * this.border[3], bound - sign * this.border[1]];
    }
    getBarPos(sec){
        let pos = this.data2pos(sec, 0);
        if(this.viewDataBorder[2] < this.border[2] && sec > (this.viewDataBorder[0] + this.viewDataBorder[2]) / 2 && sec <=this.viewDataBorder[2]) {
            let center = this.data2pos((this.viewDataBorder[0] + this.viewDataBorder[2]) / 2, 0);
            this.setSelectedArea(pos, pos);
            this.backupStateDataBorder();
            this.moveView(center);
        }
        return this.data2pos(sec, 0)[0];
    }
    getXViewBorder(){
        return [Math.floor(this.viewDataBorder[0] / 62.5), this.viewDataBorder[1],
            Math.floor(this.viewDataBorder[2] / 62.5), this.viewDataBorder[3]];
    }
    getScale(){
      return [(this.border[2] - this.border[0]) / (this.viewDataBorder[2] - this.viewDataBorder[0]),
        (this.border[3] - this.border[1]) / (this.viewDataBorder[3] - this.viewDataBorder[1])]
    }
    moveView(pos, dt=1){
        let xdt = 1, ydt = 1;
        this.currentCenter = pos;
        if(this.currentCenter[0] < this.clickCenter[0])
            xdt = -1;
        if(this.currentCenter[1] < this.clickCenter[1])
            ydt = -1;
        let [x1, y1, x2, y2] = this.selectedData();
        let xdis = x2 - x1, ydis = y2 - y1;
        this.viewDataBorder[0] = this.stateDataBorder[0] - xdis * xdt;
        this.viewDataBorder[1] = this.stateDataBorder[1] - ydis * ydt;
        this.viewDataBorder[2] = this.stateDataBorder[2] - xdis * xdt;
        this.viewDataBorder[3] = this.stateDataBorder[3] - ydis * ydt;
        this.formatViewBorder();
    }

    scaleView(scale, pos){
      if (scale == 1){
        return;
      }
      if (scale > 1){
          if(this.viewDataBorder[0] == this.border[0] && this.viewDataBorder[1] == this.border[1]
            && this.viewDataBorder[2] == this.border[2] && this.viewDataBorder[3] == this.border[3])
            return;
      }
      if (scale < 1){
          if(this.viewDataBorder[2] - this.viewDataBorder[0] < 1)
              return;
          if(this.viewDataBorder[2] - this.viewDataBorder[0] <= this.xlimit || this.viewDataBorder[3] - this.viewDataBorder[1] <= this.ylimit)
              return;
      }
      let x, y, w, h, offsetX, offsetY;
      //[x, y] = this.pos2data(pos[0], pos[1], false);
      w = this.viewDataBorder[2] - this.viewDataBorder[0];
      h = this.viewDataBorder[3] - this.viewDataBorder[1];
      //offsetX = (x - this.viewDataBorder[0]) * scale;
      //offsetY = (y - this.viewDataBorder[1]) * scale;
      //w = w * scale;
      //h = h * scale;
      [x, y] = this.pos2data(pos[0] - pos[0] * scale, pos[1] - pos[1] * scale)
      if(!this.blockX){
          this.viewDataBorder[0] = x;
          this.viewDataBorder[2] = this.viewDataBorder[0] + w * scale;
      }
      if(!this.blockY){
          this.viewDataBorder[1] = y;
          this.viewDataBorder[3] = this.viewDataBorder[1] + h * scale;
      }
      this.formatViewBorder(false);
    }

    smallView(){
        let x1, y1, x2, y2;
        [x1, y1, x2, y2] = this.selectedData();
        return [x1 + 2, y1 + 2, x2 - 2, y2 - 2]
    }
    pushInStack(cover=false, load=null){
     if(cover){
       this.dataStack[this.stackCur - 1] = [copy(this.paintedData), copy(this.paintedColor)];
            return;
       }
     let val, c;
     if(load != null){
         let data = copy(this.dataStack[load]);
         val = data[0];
         c = data[1];
     }else{
         val = copy(this.paintedData);
         c = copy(this.paintedColor);
     }
      if(this.stackCur == this.dataStack.length)
        this.dataStack.push([val, c]);
      else
        this.dataStack[this.stackCur] = [val, c];
      if(this.dataStack.length > 30)
        this.dataStack = this.dataStack.slice(1, this.dataStack.length - 1);
      else
          this.stackCur ++;
      if(this.stackCur != this.dataStack.length)
          this.dataStack = this.dataStack.slice(0, this.stackCur);
    }
    replace(c=0, reverse=false, revise=false){
        this.pushInStack(true);
      let ind = 0, data = this.paintedData, color = this.paintedColor, x1, x2, y1, y2, a, b;
      [x1, y1, x2, y2] = this.selectedData();
      while(ind > -1){
        [a, b] = [data[ind][0], data[ind][1]];
        if(a >= x1 && b >= y1 && a < x2 && b < y2){
          if(!reverse){
              color[ind] = c;
              if(revise)
                data[ind][3] = 1;
          }
        }else if (reverse){
            color[ind] = c;
        }
        ind = data[ind][2];
      }
      this.pushInStack();
    }
    crop(resize=true, reverse=true, c=1, revise=false){
        let x1, y1, x2, y2;
        [x1, y1, x2, y2] = this.selectedData();
        if(x2 - x1 < 0 || y2 - y1 < 0){
            ElMessage('Please select a larger clip.')
            return;
        }
        if(this.blockX){
            x1 = this.border[0];
            x2 = this.border[2];
        }
        if(this.blockY){
            y1 = this.border[1];
            y2 = this.border[3];
        }
        this.replace(c, reverse, revise);
        if(resize){
            this.viewDataBorder = [x1, y1, x2, y2];
            this.resetSelectedArea();
        }

    }
    filter(){
        this.crop(false, true);
        let c = this.paintedColor;
        this.undo();
        return c;
    }
    revise(){
        this.crop(false, false, this.selectedTrk + 1, true);
    }
    erase(){
        this.crop(false, false, 1, true);
    }
    reset(){
        [this.paintedData, this.paintedColor] = this.dataStack[0];
        this.stackCur = 1;
        this.resetSelectedArea();
        this.resetViewBorder();
    }
    undo(){
        if(this.stackCur <= 1)
            return;
        [this.paintedData, this.paintedColor] = this.dataStack[this.stackCur - 2];
        this.stackCur --;
    }
    redo(){
        if(this.stackCur === this.dataStack.length)
            return;
        [this.paintedData, this.paintedColor] = this.dataStack[this.stackCur];
        this.stackCur ++;
    }
}
function createCanvasData(paintedData, paintedColor, canvasW, canvasH, blockX, blockY, brushSize){
    let canvasData = new CanvasData(paintedData, paintedColor, canvasW, canvasH, blockX, blockY, brushSize);
    return canvasData;
}
export {createCanvasData}
